.BusinessAccountDetail {
  font-family: Roboto, "sans-serif";
}

.Dropdown_Header {
  display: flex;
  justify-content: space-between;
}

.Dropdown_Header h1 {
  font-size: 18px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #24272c;
}

.Dropdown_Header p {
  opacity: 0.5;
  font-family: Roboto;
  font-size: 13px;
  font-weight: bold;
  text-decoration: underline;
  line-height: 1.17;
  text-align: right;
  color: #3b414a;
}

.Dropdown_Container {
  position: relative;
  width: 75%;
  margin: 5% auto;
  padding-bottom: 5%;
}

.Dropdown_Container::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 1px;
  opacity: 0.1;
  background-color: #24272c;
}
